// React
import { Component, createElement, Fragment } from "react";

// Components
import { Collapse, Dropdown } from "@elements";

// Librarys
import { Link } from "react-router-dom";
import { FontAwesomeIcon } from "@fortawesome/react-fontawesome";

// JS
import { PATH } from "@assets/js/keys";
import { getKey } from "@assets/js/localStorage";
import getWindowSize from "@assets/js/getWindowSize";

// JSON
const navigation = require("@assets/json/navigation");

// Logos
const logoSeytu = require("@assets/img/header/logo-seytu.webp").default;
const logoOmnilife = require("@assets/img/header/logo-omnilife.webp").default;
const logoOmnilifeMenu =
  require("@assets/img/header/logo-omnilife-menu.webp").default;

class Header extends Component {

  shouldComponentUpdate() {
    return false;
  }

  render() {
    return (
      <Fragment>
        <Background />
        <Navigation />
      </Fragment>
    );
  }
}

export default Header;

/**
 * Fondo de cabezera
 */
class Background extends Component {
  constructor(props) {
    super(props);
    this.state = {
      showHeaderBackground: null,
    };
    this.handleShowHeaderBackground =
      this.handleShowHeaderBackground.bind(this);
  }

  componentDidMount() {
    this.handleShowHeaderBackground();
    window.addEventListener("resize", this.handleShowHeaderBackground);
  }

  componentWillUnmount() {
    window.removeEventListener("resize", getWindowSize);
  }

  handleShowHeaderBackground() {
    const { isGreaterThanMQ } = getWindowSize();
    if (this.state.showHeaderBackground !== isGreaterThanMQ) {
      this.setState({ showHeaderBackground: isGreaterThanMQ });
    }
  }

  render() {
    if (!this.state.showHeaderBackground) {
      return null;
    }

    return (
      <header>
        <img
          src={logoOmnilife}
          alt="logo-omnilife"
          width="1280"
          height="720"
          className="logo-omnilife"
        />
      </header>
    );
  }
}

/**
 * Estilos Personalizados
 */
const styles = {
  icon: {
    marginLeft: 5,
  },
};

/**
 * Renderizar un Item en la navegacion
 */

class NavItem extends Component {
  static defaultProps = {
    tag: "li",
    type: "link",
    onClickItem: function () {
      return null;
    },
  };

  constructor(props) {
    super(props);
    this.renderItem = this.renderItem.bind(this);
    this.handleChangePath = this.handleChangePath.bind(this);
    this.content = (
      <NavLink
        icon={this.props.icon}
        name={this.props.name}
        showArrow={this.props.showArrow}
      />
    );
  }

  shouldComponentUpdate() {
    return false;
  }

  handleChangePath() {
    const { type, onClickItem } = this.props;
    if (type !== "text") {
      onClickItem();
    }
  }

  renderItem() {
    const { tag, path } = this.props;
    return createElement(
      tag,
      {
        className: "navigation-item pointer",
        onClick: this.handleChangePath,
      },
      <Fragment>
        {this.props.type === "text" ? (
          <span className="link pointer">{this.content}</span>
        ) : (
          <Link to={path} className="link" target={path.target} rel="noreferrer">
            {this.content}
          </Link>
        )}
        {this.props.children}
      </Fragment>
    );
  }

  render() {
    const Item = this.renderItem();
    return Item;
  }
}

/**
 * Renderizar un Enlace en la navegacion
 */
class NavLink extends Component {
  static defaultProps = {
    showArrow: false,
  };

  shouldComponentUpdate() {
    return false;
  }

  render() {
    return (
      <Fragment>
        <FontAwesomeIcon icon={this.props.icon} />
        &nbsp;{this.props.name}
        {this.props.showArrow && (
          <FontAwesomeIcon style={styles.icon} icon="caret-down" />
        )}
      </Fragment>
    );
  }
}

/**
 * Enlaces de navegacion
 */
const renderNavigationItems = (handleChangePath) => {
  const savedPath = getKey(PATH);
  return navigation.map((item) => {
    if (item.submenu) {
      const subItems = item.submenu.map((subitem) => (
        <NavItem
          {...subitem}
          key={subitem.id}
          tag="div"
          onClickItem={handleChangePath}
        />
      ));
      if (window.innerWidth <= 1024) {
        return (
          <Collapse key={item.id}>
            <NavItem
              {...item}
              showArrow
              type="text"
              items={subItems}
              onClickItem={handleChangePath}
            />
          </Collapse>
        );
      }
      return (
        <li key={item.id} className="navigation-item">
          <Dropdown
            subItems={subItems.map((subitem) => {
              return { el: subitem };
            })}
          >
            <span className="link pointer">
              <NavLink name={item.name} icon={item.icon} showArrow />
            </span>
          </Dropdown>
        </li>
      );
    }
    return (
      <NavItem
        {...item}
        key={item.id}
        onClickItem={handleChangePath}
        statusx={item.path === savedPath}
      />
    );
  });
};

class Navigation extends Component {
  constructor(props) {
    super(props);
    this.state = {
      showMenu: null,
    };
    this.handleShowMenu = this.handleShowMenu.bind(this);
  }

  handleShowMenu() {
    const { isGreaterThanMQ } = getWindowSize();
    if (this.state.showHeaderBackground !== isGreaterThanMQ) {
      this.setState({ showMenu: isGreaterThanMQ });
    }
  }
  componentDidMount() {
    this.handleShowMenu();
    window.addEventListener("resize", this.handleShowMenu);
  }

  componentWillUnmount() {
    window.removeEventListener("resize", getWindowSize);
  }

  render() {
    const navigationItems = renderNavigationItems();
    return (
      <nav id="navigation">
        <img
          id="logo-seytu"
          src={logoSeytu}
          alt="seytu-logo"
          width="25"
          height="25"
        />
        {this.state.showMenu === false ? (
          <Menu />
        ) : (
          <ul className="navigation-item-wrapper">{navigationItems}</ul>
        )}
      </nav>
    );
  }
}

class Menu extends Component {
  constructor(props) {
    super(props);
    this.state = {
      isOpen: false,
    };
    this.openMenu = this.openMenu.bind(this);
    this.closeMenu = this.closeMenu.bind(this);
  }

  openMenu() {
    this.setState({ isOpen: true });
  }

  closeMenu() {
    this.setState({ isOpen: false });
  }

  render() {
    const classMenu = this.state.isOpen
      ? "navigation-item-wrapper open"
      : "navigation-item-wrapper";
    const navigationItems = renderNavigationItems(this.closeMenu);
    return (
      <Fragment>
        {this.state.isOpen ? (
          <FontAwesomeIcon
            icon="times"
            id="close-menu"
            className="pointer"
            onClick={this.closeMenu}
          />
        ) : (
          <FontAwesomeIcon
            icon="bars"
            id="menu"
            className="pointer"
            onClick={this.openMenu}
          />
        )}

        <ul className={classMenu}>
          {navigationItems}
          <figure>
            <Link to="/" rel="noreferrer">
              <img
                id="logo-omnilife"
                src={logoOmnilifeMenu}
                alt="logo_omnilife"
                width="342"
                height="103"
                loading="lazy"
              />
            </Link>
          </figure>
        </ul>
      </Fragment>
    );
  }
}
